.profile {
  font-family: Source Han Sans CN;

  .card-title,
  .card-msg {
    font-weight: 400;
    color: #040e34;
  }

  .card-title {
    font-size: 0.18rem;
    margin-top: 0.26rem;
    margin-left: 0.3rem;
  }

  .card-msg {
    height: 0.38rem;
    font-size: 0.14rem;

    .card-msg-item {
      display: block;

      &:nth-child(2) {
        font-size: 13px;
      }

      .msg-item-strong {
        color: #1bc5bd;
        margin-left: 0.04rem;
      }

      .item-remind {
        color: #1bc5bd;

        .item-remind-block {
          display: inline-block;
          width: 0.41rem;
          height: 0.1rem;
          background: #1bc5bd;
          opacity: 1;
          margin-left: 0.02rem;

          &:nth-child(2) {
            border-radius: 0.2rem 0px 0px 0.2rem;
            margin-left: 0.04rem;
          }

          &:nth-child(3) {
            border-radius: 0px;
          }

          &:nth-child(4) {
            border-radius: 0px 0.2rem 0.2rem 0px;
            margin-right: 0.08rem;
          }
        }
      }
    }
  }

  .card-form {
    margin: 0.36rem auto 0;
    width: 90%;

    label {
      font-size: 13px;
      font-weight: 400;
      color: #040e34;
      margin-right: 0.18rem;
    }

    .ant-input-number,
    .ant-input {
      background: #f2f2f7;
    }

    .ant-select,
    .ant-input {
      width: 80%;
      border-radius: 4px;
      color: #9696a3;
    }

    .ant-select-selector {
      border: none;
    }

    .form-input {
      background: #ffffff;
      border: 1px solid #d9dbe1;
    }

    .revise-button {
      width: 0.26rem;
      height: 13px;
      font-size: 13px;
      line-height: 0px;
      color: #5c5ee4;
      opacity: 1;
    }

    .sub-button {
      height: 40px;
      background: #696ae6;
      opacity: 1;
      border-radius: 4px;
      font-size: 14px;
      font-weight: 400;
      border: none;
    }

    .extra-text {
      display: flex;
      flex-direction: row;
      font-size: 13px;
      font-weight: 400;
      color: #040e34;
      margin-top: 8px;

      .extra-icon {
        height: 18px;
        width: 18px;
        border-radius: 50%;
        background: #ffa800;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #ffffff;
        font-size: 13px;
        margin-right: 7px;
      }
    }
  }

  .phone-modal {
    border-radius: 4px;
  }

  .modif-remind {
    .remind-img {
      width: 0.64rem;
      margin: 0.32rem auto 0.32rem;
    }

    .remind-text {
      width: 100%;
      color: #525666;
      margin: 0 auto;
      display: flex;
      justify-content: center;
    }
  }

  .modal-phone {
    .modal-title {
      .title-main-text {
        height: 18px;
        font-size: 18px;
        font-weight: 500;
        line-height: 18px;
        color: #040e34;
      }

      .title-vice-text {
        margin-top: 8px;
        color: #9696a3;
        font-size: 12px;
      }
    }

    .modal-form {
      margin-top: 0.4rem;
      width: 100%;

      .form-error {
        margin-bottom: 10px;
      }

      .form-item {
        height: 40px;
        display: flex;
        justify-content: flex-start;
        margin-bottom: 24px;

        .item-label {
          width: 20%;
          height: 40px;
          line-height: 40px;
        }

        .select-input {
          display: flex;
          align-items: center;
          width: 100%;

          .ant-select-selector {
            background: #F2F2F7;
            border: none;
          }

          .modal-code-input {
            height: 40px;
          }

          .code-btn {
            height: 40px;
            background: #42C2FB;
            color: #FFF;
            margin-left: 8px;
            border-radius: 4px;
          }

          .unactive {
            height: 40px;
            margin-left: 8px;
            border-radius: 4px;
          }
        }

        .item-bg {
          background: #F2F2F7;
        }
      }

      .ant-input {
        font-size: 13px;
      }

      .form-bottom {
        margin-top: 48px;
        display: flex;
        flex-direction: row-reverse;

        .close-button {
          background: #f2f2f7;
          border-radius: 4px;
          margin-left: 0.08rem;
          color: #525666;
          border: none;
          height: 40px;
        }

        .sub-button {
          background: #696ae6;
          color: #ffffff;
          border-radius: 4px;
          border: none;
          height: 40px;
        }
      }
    }



    .itemCode {
      display: flex;

      .code-btn {
        width: 79px;
        margin-left: 8px;
        background: #42c2fb;
        color: #ffffff;
        border-radius: 4px;
      }

      .unactive {
        margin-left: 8px;
      }
    }

    .modal-footer {
      display: flex;
      flex-direction: row-reverse;
      margin-top: 0.48rem;

      .close-button {
        background: #f2f2f7;
        border-radius: 4px;
        margin-left: 0.08rem;
        color: #525666;
        border: none;
        height: 40px;
      }

      .sub-button {
        background: #696ae6;
        color: #ffffff;
        border-radius: 4px;
        border: none;
        height: 40px;
      }
    }
  }

  .ant-input {
    background: #f2f2f7;
  }
}

.swal2-popup {
  font-size: 0.2rem;
}

.com-content{
  height: auto;
}
@primary-color: #5C5EE4;